<template>
  <div class="MonHocToan">
    <raw-loader :mytxt=" require('!raw-loader!./UseSelect22.vue').default "></raw-loader> 
    <h1>This is an Toan page</h1>
    <div>
      Lorem Ipsum is simply dummy text of the printing and typesetting industry. 
      Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, 
      when an unknown printer took a galley of type and scrambled it to make a type specimen book. 
      It has survived not only five centuries, 
    </div>

    <b-modal ref="my-modal2" id="modal-1" size="sm" :no-close-on-backdrop="true" hide-footer title="BootstrapVue">
      <form @submit.stop.prevent="onSaveModal">
        <div>
          <div class="form-group row">
            <label class="col-sm-4 col-form-label">Id</label>
            <div class="col-sm-8">
              <input type="text" class="form-control" v-model="r.Id" />
            </div>
          </div>

          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Name
            </label>
            <div class="col-sm-8">
              <input 
                type="text" 
                v-model="r.Name"
                v-validate="'required'"
                name="Name"
                :class=" 'form-control' " 
              />
            </div>
          </div>

          <div class="form-group row">
            <label class="col-sm-4 col-form-label">
              Thành phố
            </label>
            <div class="col-sm-8">

              <v-select
                v-model="r.Tp"
                v-validate="'required'"
                :reduce="(option) => option.id"
                :options="[
                  { label: 'Huế', id: 'HUE' },
                  { label: 'Sài gòn', id: 'SG' },
                  { label: 'Đà nẵng', id: 'DN' },
                ]"
              />

            </div>
          </div>


        </div>  
        <div class="float-right">
          <button type="submit" class="btn btn-primary mr-1 btn-sm">Save</button>
          <button type="button" class="btn btn-sm" @click="hideModal()">Cancel</button>
        </div>
      </form>
    </b-modal>

    <button class="btn btn-primary btn-sm m-1" @click="onAdd">Add</button>

    <v-select :options="['Canada', 'United States']"></v-select>    



  </div>
</template>
<script>


import Vue from "vue";
import VeeValidate from "vee-validate";
Vue.use(VeeValidate, {
  classes: true,
});

import vSelect from 'vue-select'
Vue.component('v-select', vSelect)
import 'vue-select/dist/vue-select.css';

export default {
  name: 'Tpl',
  components: {
  },
  data(){
    return{
      r:{
        Id:1,
        Name:"thuc 101",
        Tuoi:100,
        Tp:'DN'
      },
    }
  },
  methods:{
    onSaveModal(){
      this.$validator.validateAll().then((result) => {
        if (!result) {
          this.toast("Không hợp lệ");
          return;
        }else{
          alert(JSON.stringify(this.r));
          this.hideModal();
        }
      })
    },    
    onAdd(){
      this.showModal();
    },
    toast(msg, variant = null) {
      this.$bvToast.toast(msg, {
        title: `Information`,
        variant: variant,
        solid: true,
      });
    },
    showModal() {
        this.$refs['my-modal2'].show()
    },
    hideModal() {
      this.$refs['my-modal2'].hide()
    },
  }
}
</script>


