export default "<template>\r\n  <div class=\"MonHocToan\">\r\n    <raw-loader :mytxt=\" require('!raw-loader!./UseSelect22.vue').default \"></raw-loader> \r\n    <h1>This is an Toan page</h1>\r\n    <div>\r\n      Lorem Ipsum is simply dummy text of the printing and typesetting industry. \r\n      Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, \r\n      when an unknown printer took a galley of type and scrambled it to make a type specimen book. \r\n      It has survived not only five centuries, \r\n    </div>\r\n\r\n    <b-modal ref=\"my-modal2\" id=\"modal-1\" size=\"sm\" :no-close-on-backdrop=\"true\" hide-footer title=\"BootstrapVue\">\r\n      <form @submit.stop.prevent=\"onSaveModal\">\r\n        <div>\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">Id</label>\r\n            <div class=\"col-sm-8\">\r\n              <input type=\"text\" class=\"form-control\" v-model=\"r.Id\" />\r\n            </div>\r\n          </div>\r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">\r\n              Name\r\n            </label>\r\n            <div class=\"col-sm-8\">\r\n              <input \r\n                type=\"text\" \r\n                v-model=\"r.Name\"\r\n                v-validate=\"'required'\"\r\n                name=\"Name\"\r\n                :class=\" 'form-control' \" \r\n              />\r\n            </div>\r\n          </div>\r\n\r\n          <div class=\"form-group row\">\r\n            <label class=\"col-sm-4 col-form-label\">\r\n              Thành phố\r\n            </label>\r\n            <div class=\"col-sm-8\">\r\n\r\n              <v-select\r\n                v-model=\"r.Tp\"\r\n                v-validate=\"'required'\"\r\n                :reduce=\"(option) => option.id\"\r\n                :options=\"[\r\n                  { label: 'Huế', id: 'HUE' },\r\n                  { label: 'Sài gòn', id: 'SG' },\r\n                  { label: 'Đà nẵng', id: 'DN' },\r\n                ]\"\r\n              />\r\n\r\n            </div>\r\n          </div>\r\n\r\n\r\n        </div>  \r\n        <div class=\"float-right\">\r\n          <button type=\"submit\" class=\"btn btn-primary mr-1 btn-sm\">Save</button>\r\n          <button type=\"button\" class=\"btn btn-sm\" @click=\"hideModal()\">Cancel</button>\r\n        </div>\r\n      </form>\r\n    </b-modal>\r\n\r\n    <button class=\"btn btn-primary btn-sm m-1\" @click=\"onAdd\">Add</button>\r\n\r\n    <v-select :options=\"['Canada', 'United States']\"></v-select>    \r\n\r\n\r\n\r\n  </div>\r\n</template>\r\n<script>\r\n\r\n\r\nimport Vue from \"vue\";\r\nimport VeeValidate from \"vee-validate\";\r\nVue.use(VeeValidate, {\r\n  classes: true,\r\n});\r\n\r\nimport vSelect from 'vue-select'\r\nVue.component('v-select', vSelect)\r\nimport 'vue-select/dist/vue-select.css';\r\n\r\nexport default {\r\n  name: 'Tpl',\r\n  components: {\r\n  },\r\n  data(){\r\n    return{\r\n      r:{\r\n        Id:1,\r\n        Name:\"thuc 101\",\r\n        Tuoi:100,\r\n        Tp:'DN'\r\n      },\r\n    }\r\n  },\r\n  methods:{\r\n    onSaveModal(){\r\n      this.$validator.validateAll().then((result) => {\r\n        if (!result) {\r\n          this.toast(\"Không hợp lệ\");\r\n          return;\r\n        }else{\r\n          alert(JSON.stringify(this.r));\r\n          this.hideModal();\r\n        }\r\n      })\r\n    },    \r\n    onAdd(){\r\n      this.showModal();\r\n    },\r\n    toast(msg, variant = null) {\r\n      this.$bvToast.toast(msg, {\r\n        title: `Information`,\r\n        variant: variant,\r\n        solid: true,\r\n      });\r\n    },\r\n    showModal() {\r\n        this.$refs['my-modal2'].show()\r\n    },\r\n    hideModal() {\r\n      this.$refs['my-modal2'].hide()\r\n    },\r\n  }\r\n}\r\n</script>\r\n\r\n\r\n<style lang=\"scss\">\r\n\r\n  $textSize: 6.5px; //14px !default; //table text size\r\n  @import \"~tabulator-tables/src/scss/tabulator.scss\";\r\n  \r\n  \r\n  .tabulator-col-title {\r\n    text-align: center;\r\n  }\r\n  .tabulator .tabulator-footer .tabulator-page.active {\r\n    background-color: greenyellow !important;\r\n    color: blue !important;\r\n  }\r\n  .select2,\r\n  .select2-selection {\r\n    width: 100% !important;\r\n  }\r\n  .select2-container .select2-selection--single {\r\n    height: 27px;\r\n  }\r\n  .select2-container--default .select2-selection--single .select2-selection__rendered {\r\n    color: #444;\r\n    line-height: 26px;\r\n  }\r\n  .datepicker td,\r\n  th {\r\n    text-align: center;\r\n    padding: 0px 6px;\r\n    font-size: 10px;\r\n  }\r\n  .col-4 input {\r\n    margin-bottom: 1px;\r\n  }\r\n  .my-invalid .select2-container {\r\n    border: 1px solid red;\r\n    border-radius: 5px;\r\n  }\r\n  .form-group {\r\n    margin-bottom: 0.3rem;\r\n  }\r\n  .col-form-label {\r\n    padding-top: calc(0.375rem + 1px);\r\n    padding-bottom: calc(0.375rem + 1px);\r\n    margin-bottom: 0;\r\n    font-size: inherit;\r\n    line-height: 1.5;\r\n    font-weight: bold;\r\n  }\r\n  \r\n  .vs__dropdown-toggle {\r\n    -webkit-appearance: none;\r\n    -moz-appearance: none;\r\n    appearance: none;\r\n    background: none;\r\n    border: var(--vs-border-width) var(--vs-border-style) var(--vs-border-color);\r\n    border-radius: var(--vs-border-radius);\r\n    display: flex;\r\n    padding: 0 0 0px;\r\n    white-space: normal;\r\n    /*border: 1px solid red;*/\r\n  }\r\n  \r\n  .ko-hop-le-select2 {\r\n    border: 1px solid red;\r\n    border-radius: 10px;\r\n  }\r\n  .ko-hop-le{\r\n    border: 1px solid red;\r\n    border-radius: 10px;\r\n  }\r\n  </style>\r\n  \r\n";